import enquire from 'enquire.js';
import string from 'lodash/string';
import Const from '../const';

class BaseClass {
	
	constructor() {

		this.breakpoint = undefined
		this.children = undefined
		
		/**
		 * Params
		 */

		this.params = {
			disactiveOn: []
		}

		/**
		 * 0 = destroyed
		 * 1 = initialized
		 */
		this.state = 0

	}

	/**
	 * Empty methods
	 */
	
	onResize() {}
	onScroll() {}
	onHashChange(hash) {}
	onMouseWheelUp() {}
	onMouseWheelDown() {}
	onChangeBreakPoint(b) {}
	onRaf() {}
	onKeyEsc() {}
	onKeyEnter() {}
	onKeyLeft() {}
	onKeyRight() {}
	onKeyUp() {}
	onKeyDown() {}
	init() {}
	destroy() {}
	
	/**
	 * Base class methods
	 */

	onRafBaseClass() {
		if (this.state == 1) this.onRaf()
	}

	initBaseClass() {

		/**
		 * Save data attr to class
		 */

		for (var attr in this.DOM.wrapper.data()) {

		 	if (attr != 'ctrl' && attr != 'ctrlId') {
		 	
		 		let name = string.camelCase(attr.replace('ctrl', ''))
		 		let value = this.DOM.wrapper.data(attr)
	 			
	 			this.params[name] = value		 	

		 	}

		}

		if (this.params.init === undefined) this.params.init = true
	
		this.saveDomObjects()

		/**
		 * Save child to class
		 */

		this.children = $(document.body).triggerHandler('getControllersByWrapper', [this.DOM.wrapper, true])

	}
	
	onChangeBreakPointBaseClass(b) {
		
		/**
		 * Save breakpoint on class
		 */
		
		this.breakpoint = b

		/**
		 * Call init or destroy methods, based on active breakpoints
		 */

		if (this.getIfCtrlIsInitializable()) {
			
			if (this.state == 0) this.init()
			this.state = 1
		
		} else {

			if (this.state == 1) this.destroyBaseClass()
			this.state = 0
		
		}

		/**
		 * If active, call on Change Breakpoint
		 */
		
		if (this.state == 1) this.onChangeBreakPoint(this.breakpoint)

	}

	resetDomObjects() {

		for (var o in this.DOM) {

			if (
				o != 'wrapper' &&
				o != 'clonedWrapper'
			) {
				delete this.DOM[o]
			}

		}

	}

	saveDomObjects() {

		this.resetDomObjects()
 	
 		 $('[data-ctrl-dom]', this.DOM.wrapper).each((index, value) => {
			
			let parentsCtrl = $(value).parents('[data-ctrl]')

			if (
				parentsCtrl.length == 0
				|| parentsCtrl.length > 0
				&& parentsCtrl.eq(0).data('ctrl-id') == this.ID
			) {

			 	let name = string.camelCase($(value).data('ctrl-dom'))
				this.DOM[name] = this.DOM[name] ? this.DOM[name].add($(value)) : $(value)
			}

 		 })
	
	}

	destroyBaseClass(children = true) {
	
		// let wrapper = $('[data-ctrl-id=' + this.ID + ']');

		// wrapper.wrap('<div></div>');
		// let parent = wrapper.parent()
		// parent.html('')
		// parent.append(this.DOM.clonedWrapper.clone())
	
		// this.DOM.wrapper = $(parent.children()[0])
		// this.DOM.wrapper.unwrap()
		this.destroy()
		
		// this.saveDomObjects()

		// if (!children) return;

    	// for (var c of this.children) c.onHMRBaseClass()

	}

	onMouseWheelBaseClass(e) {

		this[e.deltaY > 0 ? 'onMouseWheelUp' : 'onMouseWheelDown']()

	}

	onHMRBaseClass() {

		if (this.getIfCtrlIsInitializable()) {
			
			this.destroyBaseClass()
			this.init()
			this.state = 1

		}

	}

	/**
	 * Utils
	 */

	getIfCtrlIsInitializable() {
		if (!this.params.init) return false
		return this.params.disactiveOn.indexOf(this.breakpoint) == -1
	
	}

}

module.exports = BaseClass


