import BaseClass from '../../js/class/BaseClass';

class Accordion extends BaseClass {
	
	constructor() {

		super()
	
	}

	init() {

		this.DOM.wrapper.css('border', `5px solid black`)

		this.DOM.content.hide()

		this.DOM.btn.on('click', () => {
			this.toggle()
		})

	}

	toggle() {

		this.isOpen ? this.close() : this.open()

	}

	open() {

		this.isOpen = true
		this.DOM.content.stop().slideDown()

	}

	close() {
		
		this.isOpen = false
		this.DOM.content.stop().slideUp()

	}

	destroy() {

		this.isOpen = false
	
	}

}

module.exports = Accordion
		if (module.hot) {
			module.hot.accept();
			$(document.body).trigger('hmr', Accordion);
		}
	


