let Utils = {
    randomString: (length = 7, type = 'alphanumeric') => {

		var result = [];
		var letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';
		var numbers = '0123465789';
    	length+=1

    	switch(type) {
    		case('lettera'):
    			var charSet = letters
    		break;
    		case('numbers'):
    			var charSet = numbers
    		break;
    		case('alphanumeric'):
    			var charSet = `${letters}${numbers}`
    		break;
    	}

    	while (--length) result.push(charSet.charAt(Math.floor(Math.random() * charSet.length)));
	    return result.join('');

    }
}

window.requestAnimFrame = (function(){
  return  window.requestAnimationFrame       ||
          window.webkitRequestAnimationFrame ||
          window.mozRequestAnimationFrame    ||
          function( callback ){
            window.setTimeout(callback, 1000 / 60);
          };
})();

module.exports = Utils


