import Const from '../const';
import enquire from 'enquire.js';

class EnquireClass {

	constructor() {

		for (var q in Const.mediaqueries) {
				
			enquire.register(Const.mediaqueries[q], {
				
				match: ((q) => {
					
					return () => {
						
						$('html')
							.removeClass (function (index, css) {
								return (css.match (/(^|\s)mq-\S+/g) || []).join(' ');
							})
							.addClass(`mq-${q}`)

						$(document.body).trigger('enquire', q);

					}

				})(q)
			
			})

		}

	}

}

module.exports = EnquireClass


