let Const = {

    mediaqueries: {
        'desktop-medium'    : '(min-width: 1200px)',
        'desktop-small'     : '(min-width: 992px) and (max-width: 1199px)',
        'tablet'            : '(min-width: 768px) and (max-width: 991px)',
        'mobile'            : '(max-width: 767px)'
    }

}

module.exports = Const


