import $ from 'jquery';
import Utils from '../utils';
import path from 'path'
import string from 'lodash/string';
import enquireClass from './EnquireClass';

class CtrlManager {
    
	constructor() {
        
        this.controllersClass = []
		this.controllers = []
        this.breakpoint = undefined

        /**
         * Get controllers constructor from components folder
         */

        this.getControllersInstance()

        /**
         * Bind getControllersByWrapper event to body
         */

        $(document.body).on('getControllersByWrapper', (e, wrapper) => {
           
            return this.getControllersByWrapper(wrapper)
        
        })

        /**
         * Parse DOM and create controllers
         */
		
        this.controllers = this.createControllers()
        
        $(document.body).on('createControllers', (e, wrapper) => {
           
            let controllers = this.createControllers(wrapper)
            this.controllers = this.controllers.concat(controllers)
            this.callMethod('initBaseClass', {}, controllers)
            this.callMethod('onChangeBreakPointBaseClass', this.breakpoint, controllers)

            $(document.body).trigger('lazyload', wrapper)

        })

        /**
         * Bind method to window and call method on each controller
         */

        this.bindMethodsToWindow()

        /**
         * Initialize controllers
         */

        this.callMethod('initBaseClass')

        /**
         * Bind RAF loop to each controller onRaf method
         */

        this.rafLoop()

        /**
         * Bind enquire listener to onChangeBreakpoint method
         * Init method will be called here
         */

        $(document.body).on('enquire', (e, data) => {
            
            this.breakpoint = data
            this.callMethod('onChangeBreakPointBaseClass', data)

        })

        /**
         * Bind key listener
         */

        $(document.body).on('keyup', (e) => {
            
            switch(e.keyCode) {

                case(27): // ESC
                    this.callMethod('onKeyEsc')
                break;
                case(13):
                    this.callMethod('onKeyEnter')
                break;
                case(37):
                    this.callMethod('onKeyLeft')
                break;
                case(39):
                    this.callMethod('onKeyRight')
                break;
                case(38):
                    this.callMethod('onKeyUp')
                break;
                case(40):
                    this.callMethod('onKeyDown')
                break;

            }

        })

        /**
         * Setting register with enquire
         */

        new enquireClass()

        /**
         * Bind HMR listener to onHMR method
         * Reset controller state
         */
		
        $(document.body).on('hmr', this.hmrCtrl.bind(this))

	}

    getControllersInstance() {
    
        const req = require.context('../../components/', true, /\.js$/);

        let controllers = req.keys().map(key => {
            let basename = path.basename(key,'.js')
            this.controllersClass.push(require(`../../components/${basename}/${basename}.js`))
        });

    }

	hmrCtrl(e, ctrl) {

        console.clear()

		const target = ctrl.prototype
		const enumAndNonenum = Object.getOwnPropertyNames(target);
		const enumOnly = Object.keys(target);
		const nonenumOnly = enumAndNonenum.filter(key => enumOnly.indexOf(key) === -1 && key !== 'constructor');

		for (var c of this.getControllersByName(ctrl.name)) {
	
			nonenumOnly.forEach((val) => {
				return c.__proto__[val] = target[val]
			});

			c.onHMRBaseClass()
            
        }


	}

	rafLoop() {
        this.callMethod('onRafBaseClass')
    	window.requestAnimFrame(this.rafLoop.bind(this));
	}

	bindMethodsToWindow() {

		$(window)
            .on({
                resize: () => {
					this.callMethod('onResize')
                },
                scroll: () => {
                	this.callMethod('onScroll')
                },
                hashchange: () => {
                	let hash = window.location.hash.replace('#', '')
                	this.callMethod('onHashChange', {hash})
                }
            })
            .on('mousewheel', (e) => {
				this.callMethod('onMouseWheelBaseClass', e)
            })

	}

	getControllersInstanceByName(name) {
        
        var name = string.camelCase(name)
        name = string.upperFirst(name)
        
        return this.controllersClass.filter((i) => name == i.name)[0]
	
	}

	getControllersByName(name) {

    	return this.controllers.filter((i) => i.NAME == name)
	
	}


    getControllersByWrapper(w, children = false) {

        let controllers = []
        var wrapper = $('[data-ctrl]', w)

        wrapper.each((i, value) => {
            controllers.push(this.getControllerByWrapper(value))
        })

        return controllers

    }

    getControllerByWrapper(w, controllers = this.controllers) {


        return controllers.filter(i => i.ID == $(w).data('ctrl-id'))[0]

    }

	controllerExist(name) {
        
        return this.getControllersInstanceByName(name)

    }

	createControllers(wrapper = document.body) {

        let controllers = []
        let controllersWrapper = $()

        $('[data-ctrl]', wrapper).each((i, value) => {

        	let name = $(value).data('ctrl')

            if (
                this.controllerExist(name)
                && !this.getControllerByWrapper($(value))
            ) {
                
                var ctrl = new(this.getControllersInstanceByName(name))();
                ctrl.ID = `${name}-${Utils.randomString(5, 'numbers')}`
                

                ctrl.NAME = name

                ctrl.DOM = {}
                ctrl.DOM.wrapper = $(value);

                $(value).attr('data-ctrl-id', ctrl.ID);


                controllers.push(ctrl)
                controllersWrapper = controllersWrapper.add(ctrl.DOM.wrapper)

            }
        
        });

        /**
         * Create cloned wrapper
         */

        // controllersWrapper.each((i, value) => {
          
        //     let name = $(value).data('ctrl')
        //     let ctrl = this.getControllerByWrapper(value, controllers)

        //     ctrl.DOM.clonedWrapper = ctrl.DOM.wrapper.clone()

        // })

        return controllers

    }

    callMethod(method, params = {}, ctrl = this.controllers) {

 		for (var c of ctrl) c[method](params)

    }

}

module.exports = CtrlManager


